import {useState} from "react"

export const MainMenu = () => {

    const [timestamp, setTimestamp] = useState(new Date())

    setTimeout(() => {
        const now = new Date()
        console.log(now)
        setTimestamp(now)
    }, 1000)

    return <ul>
        <li>
            {timestamp.toLocaleTimeString()}
        </li>
        <li>Time</li>
        <li>Projects</li>
        <li>Reports</li>
    </ul>
}